* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen-Sans, Ubuntu, Cantarell, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

img {
  max-width: 100%;
  display: block;
}

button[data-start] {
  height: 25px;
  background-color: #b3daf7;
  border: 1px solid #1a0d52;
  border-radius: 4px;
  padding-left: 15px;
  padding-right: 15px;
}

button[data-start]:hover {
  cursor: pointer;
}

button[data-stop] {
  height: 25px;
  background-color: #f7b3e2;
  border: 1px solid #6b112c;
  border-radius: 4px;
  padding-left: 15px;
  padding-right: 15px;
}

button[data-stop]:hover {
  cursor: pointer;
}

.timer {
  gap: 20px;
  display: flex;
}

.field {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
}

.value {
  font-size: 50px;
}

.label {
  text-transform: uppercase;
}

#datetime-picker {
  height: 25px;
  border: 1px solid #1a0d52;
  border-radius: 4px;
}

